/* General Styles */
body, html {
    background-color: #1c1c27;
    color: #f5f5f5;
    font-family: "Roboto", sans-serif;
    margin: 0;
    padding: 0;
    height: 100%;
}

.add-user-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    padding: 20px;
    position: relative;
    padding-top: 60px;  
}

.back-arrow,
.menu-icon {
    position: fixed; 
    top: 10px;
    font-size: 24px;
    background: none;
    border: none;
    color: #00aaff;
    cursor: pointer;
    
}


.back-arrow {
    left: 10px;
}

.menu-icon {
    right: 10px;
}


.add-user-container {
    background-color: #2e2e42;
    border-radius: 12px;
    padding: 2rem;
    width: 100%;
    max-width: 450px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.6);
    position: relative;
    z-index: 1;  
}

.user-form label {
    margin-top: 1rem;
    font-size: 0.95rem;
    color: #b0b0b0;
}

.user-form input, .user-form select {
    margin-top: 0.5rem;
    padding: 0.8rem;
    font-size: 0.95rem;
    border-radius: 6px;
    border: 1px solid #444;
    background-color: #222;
    color: #f5f5f5;
}

.user-form input:focus, .user-form select:focus {
    border-color: #00aaff;
    outline: none;
}

.button-group {
    display: flex;
    gap: 1rem;
    margin-top: 2rem;
}

.add-user-button, .share-credentials-button {
    padding: 0.8rem 1.2rem;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
}

.add-user-button:hover, .share-credentials-button:hover {
    background-color: #0056b3;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.modal-content {
    background-color: #2e2e42;
    padding: 2rem;
    border-radius: 12px;
    width: 90%;
    max-width: 400px;
    text-align: center;
}

.modal-content input {
    width: 100%;
    padding: 0.8rem;
    margin: 1rem 0;
    border: 1px solid #444;
    border-radius: 6px;
    background-color: #222;
    color: white;
}

.modal-buttons {
    display: flex;
    justify-content: space-between;
}

.modal-buttons button {
    padding: 0.8rem 1.2rem;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
}

.modal-buttons button:hover {
    background-color: #0056b3;
}


@media (max-width: 768px) {
    .add-user-page {
        padding-top: 60px;  
    }

    .back-arrow,
    .menu-icon {
        position: fixed;  
        top: 10px;
        z-index: 1010;  
    }

    .back-arrow {
        left: 10px;
    }

    .menu-icon {
        right: 10px;
    }

    .add-user-container {
        padding: 2rem;
        width: 100%;
        max-width: 450px;
        box-shadow: 0 4px 20px rgba(0, 0, 0, 0.6);
        position: relative;
        z-index: 1;
    }
}
